:root {
  --font-family: "Roboto", "Arial", "Helvetica", sans-serif;
  --font-size-xl: 30px;
  --font-size-l: 20px;
  --font-size-m: 16px;
  --font-size-s: 14px;
  --color-dark: #261c00;
  --color-yellow: #f4b000;
  --color-gray: #918f8a;
  --color-gray-light: #bfbfbf;
  --color-light: #fcfbf7;
  --color-white: #ffffff;
  --color-transparent: rgba(255, 255, 255, 0);
}

