@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Regular"), local("Roboto Regular"), url("../../../assets/fonts/Roboto-Regular/Roboto-Regular.woff2") format("woff2"), url("../../../assets/fonts/Roboto-Regular/Roboto-Regular.woff") format("woff"), url("../../../assets/fonts/Roboto-Regular/Roboto-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Medium"), local("Roboto Medium"), url("../../../assets/fonts/Roboto-Medium/Roboto-Medium.woff2") format("woff2"), url("../../../assets/fonts/Roboto-Medium/Roboto-Medium.woff") format("woff"), url("../../../assets/fonts/Roboto-Medium/Roboto-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Roboto-Bold"), local("Roboto Bold"), url("../../../assets/fonts/Roboto-Bold/Roboto-Bold.woff2") format("woff2"), url("../../../assets/fonts/Roboto-Bold/Roboto-Bold.woff") format("woff"), url("../../../assets/fonts/Roboto-Bold/Roboto-Bold.ttf") format("truetype");
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  min-width: 100%;
  min-height: 100%;
}

html {
  box-sizing: border-box;
}

body {
  position: relative;
  min-width: 320px;
  padding: 0;
  font-family: var(--font-family);
  font-size: var(--font-size-m);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

a, a:hover {
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
}

.hidden {
  display: none !important;
}
@media (max-width: 767px) {
  .hidden_mobile {
    display: none !important;
  }
}
@media (max-width: 1023px) {
  .hidden_mobile-tablet {
    display: none !important;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .hidden_tablet {
    display: none !important;
  }
}
@media (min-width: 1024px) {
  .hidden_desktop {
    display: none !important;
  }
}
@media (min-width: 768px) {
  .hidden_desktop-tablet {
    display: none !important;
  }
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  padding: 0 !important;
  border: 0 !important;
  clip: rect(0, 0, 0, 0) !important;
  -webkit-clip-path: inset(100%) !important;
  clip-path: inset(100%) !important;
}

.theme, .theme_dark {
  --main-color: var(--color-light);
  --dim-color: var(--color-gray-light);
  color: var(--main-color);
  background-image: radial-gradient(70.24% 70.24% at 49.87% 29.76%, rgba(18, 12, 1, 0.9) 0%, #000000 100%);
}
.theme_light {
  --main-color: var(--color-dark);
  --dim-color: var(--color-gray);
  background-color: var(--color-white);
}

.headline {
  font-size: var(--font-size-xl);
  line-height: var(--font-size-xl)2px;
  font-weight: 700;
}

.subhead {
  font-size: var(--font-size-l);
  line-height: var(--font-size-l)2px;
  font-weight: 700;
}
.subhead_dim {
  color: var(--dim-color);
}

.main-text {
  font-size: var(--font-size-m);
  line-height: var(--font-size-m)2px;
  font-weight: 500;
}
.main-text_dim {
  color: var(--dim-color);
}

.caption {
  font-size: var(--font-size-m);
  line-height: var(--font-size-m)2px;
  font-weight: 400;
}
.caption_dim {
  color: var(--dim-color);
}

.column-bar {
  background-image: linear-gradient(180deg, var(--color-transparent) 0%, rgba(0, 0, 0, 0.8) 100%), radial-gradient(149.08% 93.3% at 38.75% 72.1%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(103, 103, 103, 0.6);
  border-radius: 6px;
}
.column-bar_active {
  background-image: radial-gradient(91.67% 122.17% at 69.17% -11.17%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0 6px 15px rgba(255, 162, 0, 0.4);
}
.column-bar_wide.column-bar_active {
  background-image: radial-gradient(81.14% 100% at 50% 0%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0 6px 15px rgba(255, 162, 0, 0.5);
}

.theme_light .column-bar {
  background-image: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0 0 20px rgba(207, 207, 207, 0.5);
}
.theme_light .column-bar_active {
  background-image: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0 0 20px rgba(255, 176, 57, 0.8);
}